
















export default {
	data() {
		return {
			msgList: [],
			}
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
				setTimeout(function() {
					uni.stopPullDownRefresh()
				}, 1000)
			},
		onLoad() {

			},
			methods: {
				// toChat(chat){
				// 	uni.navigateTo({
				// 		url:"chat/chat?name="+chat.username
				// 	})
				// }
			},
			mounted() {
				//系统消息
				uni.request({
					url: this.config.url + "member/message",
					method: "POST",
					data: {
						token: this.token
					},
					success: (res) => {
						console.log(res)

						if (res.data.code == 1) {
							this.msgList = res.data.data.data
						}
					}
				})
			}
	}
